body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.icon {
    font-size: 24px; /* increase the size to your desired value */
}
code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
    font-family: 'Syne Tactile', cursive;
}

.container:hover .top:before,
.container:hover .top:after,
.container:hover .bottom:before,
.container:hover .bottom:after {
    margin-left: 200px;
    transform-origin: -200px 50%;
    transition-delay: 0s;
}

.container:hover .center,
.container:active .center {
    opacity: 1;
    transition-delay: 0s;
}

.top:before, .bottom:before, .top:after, .bottom:after {
    content: '';
    display: block;
    position: absolute;
    width: 200vmax;
    height: 200vmax;
    top: 50%;
    left: 50%;
    margin-top: -100vmax;
    transform-origin: 0 50%;
    transition: all 1.5s cubic-bezier(0.445, 0.05, 0, 1);
    z-index: 10;
    opacity: 0.50;
    transition-delay: 0s;
}

.top:before {
    transform: rotate(45deg);
    background: #00F060;
}

.top:after {
    transform: rotate(135deg);
    background: #00FC8F;
}

.bottom:before {
    transform: rotate(-45deg);
    background: #00C89D;
}

.bottom:after {
    transform: rotate(-135deg);
    background: #00FCBF;
}

.center {
    position: absolute;
    width: 400px;
    height: 400px;
    top: 50%;
    left: 50%;
    margin-left: -200px;
    margin-top: -200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 30px;
    opacity: 0;
    transition: all 1s cubic-bezier(0.445, 0.05, 0, 1);
    transition-delay: 1s;
    color: #ffffff;
}

.center input {
    width: 100%;
    padding: 0px;
    margin: 0px;
    border-radius: 1px;
    border: 1px solid #ccc;
}


